import React, { useState, useEffect } from 'react';
import { 
  Card, 
  CardContent, 
  Typography, 
  Button, 
  Select, 
  MenuItem, 
  FormControl, 
  InputLabel,
  TextField,
  Box,
  Paper,
  Grid
} from '@mui/material';

// Event System
const eventBus = {
  events: {},
  on(event, callback) {
    this.events[event] = this.events[event] || [];
    this.events[event].push(callback);
  },
  emit(event, payload) {
    if (this.events[event]) {
      this.events[event].forEach(callback => callback(payload));
    }
  }
};

// Monster Class
class Monster {
  constructor(name, config) {
    this.name = name;
    this.hp = config.hp || 100;
    this.str = config.str || 10;
    this.dex = config.dex || 5;
    this.int = config.int || 5;
    this.wis = config.wis || 5;
    this.con = config.con || 10;
    this.actions = config.actions || [];
    this.currentHp = this.hp;
    
  }

  takeDamage(amount) {
    this.currentHp -= amount;
    if (this.currentHp < 0) this.currentHp = 0;
  }

  isAlive() {
    return this.currentHp > 0;
  }
}

// Action Class
class Action {
  constructor(name, config) {
    this.name = name;
    this.trigger = config.trigger || 'newTurn';
    this.condition = config.condition || (() => true);
    this.effect = config.effect || (() => {});
  }
}

// Create Functions
const monsters = [];

function createMonster(name, config) {
  const newMonster = new Monster(name, {
    hp: 100, str: 10, dex: 5, int: 5, wis: 5, con: 10,
    actions: [],
    ...config
  });
  monsters.push(newMonster);
  return newMonster;
}

function createAction(name, config) {
  return new Action(name, {
    trigger: 'newTurn',
    condition: () => true,
    effect: () => {},
    ...config
  });
}

// Battle Simulator
function simulateBattle(monster1, monster2) {
  let round = 0;
  let battleLog = "";
  monster1.currentHp = monster1.hp;
  monster2.currentHp = monster2.hp;

  while (monster1.isAlive() && monster2.isAlive()) {
    round++;
    battleLog += `\n--- Round ${round} --- \n`;

    eventBus.emit('newTurn', { round, monster1, monster2 });

    battleLog += processMonsterTurn(monster1, monster2, round);
    if (!monster2.isAlive()) break;

    battleLog += processMonsterTurn(monster2, monster1, round);
  }

  battleLog += `\n---\n${monster1.isAlive() ? monster1.name : monster2.name} Wins! 🏆`;
  return battleLog;
}

function processMonsterTurn(monster, opponent, round) {
  let log = "";
  for (const action of monster.actions) {
    if (action.trigger === 'newTurn' && action.condition({ round })) {
      log += executeAction(action, monster, opponent, round);
    }
  }
  return log;
}

function executeAction(action, attacker, defender, round) {
  let log = "";
  let damage = 0;
  
  if (action.effect) {
    let damageCallback = (damageAmount) => {
      damage = damageAmount;
    };
    let context = { attacker, defender, round, damageCallback };

    eventBus.emit("beforeDealDamage", context);
    action.effect(context);

    if (damage > 0) {
      eventBus.emit("dealDamage", context);
      log += `${attacker.name} 🗡️ ${defender.name} for ${damage}! `;
      defender.takeDamage(damage);
      eventBus.emit("afterDealDamage", context);
    }
  }

  log += defender.isAlive() ? "" : `${defender.name} has fallen. 💀 \n`;
  return log;
}

// Define Actions
const punchAction = createAction("Punch", {
  effect: ({ attacker, damageCallback }) => {
    damageCallback(attacker.str);
  },
});

const strongPunchAction = createAction("Strong Punch", {
  condition: ({ round }) => round % 3 === 0,
  effect: ({ attacker, damageCallback }) => {
    damageCallback(attacker.str * 2);
  }
});

const fireboltAction = createAction("Firebolt", {
  condition: ({ round }) => round % 4 === 0,
  effect: ({ attacker, damageCallback }) => {
    damageCallback(attacker.int * 2);
  }
});

const healAction = createAction("Heal", {
  condition: ({ round }) => round % 5 === 0,
  effect: ({ attacker, damageCallback }) => {
    if (attacker.currentHp < attacker.hp) {
      const healAmount = Math.min(attacker.hp - attacker.currentHp, Math.floor(attacker.wis * 1.5));
      attacker.currentHp += healAmount;
      damageCallback(-healAmount);
    } else {
      damageCallback(0);
    }
  }
});

const resistance = createAction("Resistance", {
  trigger: "beforeDealDamage",
  effect: ({ damageCallback }) => {
    damageCallback(Math.floor(damageCallback() / 2));
  }
});

const lifesteal = createAction("Lifesteal", {
  trigger: "afterDealDamage",
  effect: ({ attacker, damage }) => {
    attacker.currentHp = attacker.currentHp + (damage * 0.1);
  }
});

const dodge = createAction("Dodge", {
  trigger: "beforeDealDamage",
  condition: ({ round }) => round % 2 === 0,
  effect: ({ damageCallback }) => {
    damageCallback(0);
  }
});

// Create Monsters
createMonster("Goblin", {
  str: 8,
  dex: 7,
  actions: [punchAction, strongPunchAction]
});

createMonster("Knight", {
  str: 12,
  con: 15,
  actions: [punchAction, resistance]
});

createMonster("Mage", {
  int: 15,
  actions: [punchAction, fireboltAction, lifesteal]
});

createMonster("Priest", {
  wis: 14,
  actions: [punchAction, healAction]
});

createMonster("Rogue", {
  dex: 12,
  actions: [punchAction, dodge]
});


const demoPasteContent = `// Action for the strong robot
const devastatingStrike = createAction("Devastating Strike", {
  effect: ({ attacker, damageCallback }) => {
    damageCallback(attacker.str * 3); // Deals triple strength damage
  }
});

// Strong robot with low HP
createMonster("Glass Cannon Bot", {
  hp: 50, // Low HP
  str: 25, // Very high strength
  dex: 10,
  int: 5,
  wis: 5,
  con: 5,
  actions: [devastatingStrike]
});

// Action for the durable robot
const fortify = createAction("Fortify", {
  trigger: 'newTurn',
  effect: ({ attacker }) => {
    attacker.currentHp = Math.min(attacker.hp, attacker.currentHp + 5); // Heals 5 HP per turn
  }
});

// Durable robot with low attack
createMonster("Tank Bot", {
  hp: 200, // High HP
  str: 5, // Low strength
  dex: 5,
  int: 5,
  wis: 5,
  con: 20, // High constitution for defense
  actions: [punchAction,fortify]
});
`;
// External Functions
const handlePaste = (pasteContent, setPasteContent, setError) => {
  try {
    if (pasteContent) {
      eval(pasteContent);
      setPasteContent("");
      setError("");
    }
  } catch (err) {
    setError(`Error: ${err.message}`);
    console.error('Error executing pasted code:', err);
  }
};

const handleBattle = (monster1, monster2, monsters, setBattleLog) => {
  const selectedMonster1 = monsters.find(m => m.name === monster1);
  const selectedMonster2 = monsters.find(m => m.name === monster2);

  if (selectedMonster1 && selectedMonster2) {
    setBattleLog(simulateBattle(selectedMonster1, selectedMonster2));
  } else {
    setBattleLog("Error: Could not find selected monster");
  }
};


// Main Component

const ModBattler = () => {
  const [monster1, setMonster1] = useState("Goblin");
  const [monster2, setMonster2] = useState("Goblin");
  const [battleLog, setBattleLog] = useState("");
  const [pasteContent, setPasteContent] = useState(demoPasteContent);
  const [error, setError] = useState("");

  const getMonsterDetails = (monsterName) => {
    const monster = monsters.find(m => m.name === monsterName);
    if (!monster) return null;
    return <MonsterDisplay monster={monster} />;
  };

  return (
    <div className="max-w-4xl mx-auto mt-8 px-4">
      <div className="bg-white rounded-xl shadow-lg overflow-hidden">
        <div className="p-6">
          <h1 className="text-3xl font-bold text-center text-gray-800 mb-8">
            ⚔️ Monster Battler ⚔️
          </h1>

          {/* Monster Selection */}
          <div className="grid grid-cols-1 md:grid-cols-2 gap-4 mb-6">
            <div>
              <label className="block text-sm font-medium text-gray-700 mb-1">
                Monster 1
              </label>
              <select
                value={monster1}
                onChange={(e) => setMonster1(e.target.value)}
                className="w-full rounded-lg border-gray-300 shadow-sm focus:border-indigo-500 focus:ring-indigo-500"
              >
                {monsters.map(monster => (
                  <option key={monster.name} value={monster.name}>
                    {monster.name}
                  </option>
                ))}
              </select>
            </div>
            
            <div>
              <label className="block text-sm font-medium text-gray-700 mb-1">
                Monster 2
              </label>
              <select
                value={monster2}
                onChange={(e) => setMonster2(e.target.value)}
                className="w-full rounded-lg border-gray-300 shadow-sm focus:border-indigo-500 focus:ring-indigo-500"
              >
                {monsters.map(monster => (
                  <option key={monster.name} value={monster.name}>
                    {monster.name}
                  </option>
                ))}
              </select>
            </div>
          </div>

          {/* Monster Displays */}
          <div className="grid grid-cols-1 md:grid-cols-2 gap-6 mb-6">
            <div>{getMonsterDetails(monster1)}</div>
            <div>{getMonsterDetails(monster2)}</div>
          </div>

         

          {error && (
            <div className="mb-4 p-3 bg-red-50 text-red-700 rounded-lg">
              {error}
            </div>
          )}

          {/* Buttons */}
          <div className="grid grid-cols-1 md:grid-cols-2 gap-4 mb-6">
            <button
              onClick={() => handleBattle(monster1, monster2, monsters, setBattleLog)}
              className="w-full px-4 py-2 bg-indigo-600 text-white rounded-lg hover:bg-indigo-700 focus:outline-none focus:ring-2 focus:ring-indigo-500 focus:ring-offset-2"
            >
              Start Battle
            </button>
           
          </div>

          {/* Battle Log */}
          <div className="bg-gray-50 rounded-lg p-4 font-mono text-sm whitespace-pre-line min-h-[200px] border border-gray-200">
            <div dangerouslySetInnerHTML={{ __html: battleLog || 'Battle log will appear here...' }} />
          </div>
          
        </div>
         {/* Paste Area */}
         <textarea
            value={pasteContent}
            onChange={(e) => setPasteContent(e.target.value)}
            placeholder="Paste content here to create new monsters or actions..."
            className="w-full h-32 p-3 mb-4 rounded-lg border border-gray-300 focus:border-indigo-500 focus:ring-indigo-500"
          />
           <button
              onClick={() => handlePaste(pasteContent, setPasteContent, setError)}
              className="w-full px-4 py-2 border border-gray-300 text-gray-700 rounded-lg hover:bg-gray-50 focus:outline-none focus:ring-2 focus:ring-indigo-500 focus:ring-offset-2"
            >
              Execute Pasted Code
            </button>
      </div>
    </div>
  );
};

export default ModBattler;


const MonsterDisplay = ({ monster }) => {
  const excludedProps = ['name', 'hp', 'currentHp', 'actions', 'takeDamage', 'isAlive'];
  
  // Get all stats dynamically (excluding certain properties)
  const stats = Object.entries(monster).filter(
    ([key]) => !excludedProps.includes(key)
  );

  // Helper function to format stat names
  const formatStatName = (stat) => {
    return stat.toUpperCase();
  };

  return (
    <div className="w-full p-4 bg-white rounded-lg shadow-md border border-gray-200">
      {/* Monster Name */}
      <h2 className="text-2xl font-bold text-gray-800 mb-4 text-center">
        {monster.name}
      </h2>

      {/* HP Bar */}
      <div className="mb-4">
        <div className="flex justify-between items-center mb-1">
          <span className="text-sm font-semibold text-gray-700">HP</span>
          <span className="text-sm text-gray-600">
            {monster.currentHp}/{monster.hp}
          </span>
        </div>
        <div className="w-full bg-gray-200 rounded-full h-2.5">
          <div
            className="bg-green-600 h-2.5 rounded-full transition-all duration-300"
            style={{
              width: `${(monster.currentHp / monster.hp) * 100}%`,
              backgroundColor: monster.currentHp < monster.hp * 0.3 ? '#ef4444' : 
                             monster.currentHp < monster.hp * 0.6 ? '#f59e0b' : 
                             '#22c55e'
            }}
          />
        </div>
      </div>

      {/* Stats Grid */}
      <div className="grid grid-cols-2 md:grid-cols-3 gap-4 mb-4">
        {stats.map(([stat, value]) => (
          <div 
            key={stat}
            className="p-3 bg-gray-50 rounded-lg border border-gray-100 hover:border-gray-200 transition-colors"
          >
            <div className="text-xs text-gray-500 font-medium">
              {formatStatName(stat)}
            </div>
            <div className="text-lg font-semibold text-gray-700">
              {value}
            </div>
          </div>
        ))}
      </div>

      {/* Actions Section */}
      {monster.actions && monster.actions.length > 0 && (
        <div className="mt-4">
          <h3 className="text-sm font-semibold text-gray-700 mb-2">Actions</h3>
          <div className="grid grid-cols-2 gap-2">
            {monster.actions.map(action => (
              <div
                key={action.name}
                className="px-3 py-2 bg-gray-50 rounded text-sm text-gray-700 border border-gray-100"
              >
                {action.name}
              </div>
            ))}
          </div>
        </div>
      )}
    </div>
  );
};


// Reusable styles
const battleLogStyles = {
  p: 2,
  minHeight: 200,
  whiteSpace: 'pre-line',
  fontFamily: 'monospace',
  bgcolor: 'grey.50'
};

